<script>
import { actionTypes } from '@/store/modules/register';
import { mapState } from 'vuex';
export default {
  name: 'AppPutContact',
  data() {
    return {
      putData: [],
      items: {},
      passportData: {},
      isActiveBlock: false,
      isAddfoto: false,
      newPhoto: [],
      file: [],
    };
  },
  computed: {
    ...mapState({
      currentUser: (state) => state.register.currentUser,
      contact: (state) => state.register.contact,
    }),
  },
  components: {},
  methods: {
    putContact() {
      const forms = document.forms;
      const formData = new FormData(forms[2]);
      this.putData = [];
      for (let [name, value] of formData) {
        this.putData = { ...this.putData, [name]: value };
      }
      this.validFirstBlock();
    },
    submit() {
      this.$store
        .dispatch(actionTypes.PutContact, this.items)
        .then(() => {
          this.$emit('validFirstBlock');
        })
        .catch((er) => {
          console.log('', er);
        });
    },
    handleFileUpload(event) {
      this.file = this.$refs.file.files;
      [...this.file].forEach((child) => {
        if (!child.type.match('image')) {
          return console.log(' !');
        }
        const reader = new FileReader(); // 
        reader.onload = (ev) => {
          this.isAddfoto = true;
          this.newPhoto.push({
            src: ev.target.result,
            file: file,
          });
        };
        reader.readAsDataURL(child); // ()
      });
    },
    putImage() {
      let formData = new FormData();
      let file = this.file;
      formData.append('file', file[0]);
      formData.append('id', this.contact.data.result.userId);
      this.$store
        .dispatch(actionTypes.addPhoto, formData)
        .then(() => {
          this.isAddfoto = false;
          this.newPhoto = [];
        })
        .catch((er) => {
          console.log(er, 'Error');
        });
    },

    removeHandler(index, name) {
      this.newPhoto.splice(index, 1);
      this.isAddfoto = !this.isAddfoto;
    },
    validFirstBlock() {
      //  
      this.passportData = {
        registrationAddress: this.putData.registrationAddress,
        passportPlaceOfIssue: this.putData.passportPlaceOfIssue,
        departmentCode: this.putData.departmentCode,
        passportDateOfIssue: this.putData.passportDateOfIssue,
        passportSerial: this.putData.passportSerial,
        passportNumber: this.putData.passportNumber,
        birthDate: this.putData.birthDate,
      };
      //  
      if (
        this.putData.confidantFirstName &&
        this.putData.confidantLastName &&
        this.putData.confidantMiddleName &&
        this.putData.confidantPhone &&
        this.putData.relationship
      ) {
        this.confidantData = {
          confidantFirstName: this.putData.confidantFirstName,
          confidantLastName: this.putData.confidantLastName,
          confidantMiddleName: this.putData.confidantMiddleName,
          confidantPhone: this.putData.confidantPhone,
          relationship: this.putData.relationship,
        };
      } else this.confidantData = null;

      //  
      this.items = {
        id: this.contact.data.result.userId,
        lastName: this.putData.lastName,
        firstName: this.putData.firstName,
        middleName: this.putData.middleName,
        email: this.putData.email,
        contactPhone: this.putData.contactPhone,
        placeOfResidence: this.putData.placeOfResidence,
        passportData: this.passportData,
        confidantData: this.confidantData,
      };
      // this.$emit("validFirstBlock");
      this.submit();
    },
  },
};
</script>

