import Vue from 'vue';
import VueRouter from 'vue-router';
import Registration from '../views/Registration.vue';
import Login from '../views/Login.vue';
import Clients from '../views/Clients.vue';
import Ready from '../views/Register-ready.vue';
import AddNewReady from '../views/addBlockReady.vue';

import AddBlocks from '../views/addBlocks.vue';
import Errors from '../views/Errors.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Registration',
    component: Registration,
    meta: {
      Auth: true,
    },
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      Auth: false,
    },
  },
  {
    path: '/clients',
    name: 'clients',
    component: Clients,
    meta: {
      Auth: true,
    },
  },

  {
    path: '/add-block',
    name: 'addBlock',
    component: AddBlocks,
    meta: {
      Auth: true,
    },
  },
  {
    path: '/success',
    name: 'success',
    component: Ready,
    meta: {
      Auth: true,
    },
  },
  {
    path: '/success-add',
    name: 'success-add',
    component: AddNewReady,
    meta: {
      Auth: true,
    },
  },

  {
    path: '/*',
    component: Errors,
    // component: { render: (h) => h("div", ["404! Page Not Found!"]) },
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
