<template>
  <transition name="modal" @click="$emit('close')">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <h1 class="modal__title"> !</h1>
            <slot name="header">
              <p>     ?</p>
            </slot>
          </div>

          <div class="modal-body">
            <input
              type="button"
              value=""
              class="modal__btn"
              @click="submit()"
            />
            <input
              type="button"
              value=""
              class="modal__btn"
              @click="$emit('close')"
            />
          </div>
          <div class="modal-footer">
            <slot name="footer"> </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { actionTypes } from '@/store/modules/register';
export default {
  name: 'AppModal',
  props: {
    weapon: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    submit() {
      this.$store
        .dispatch(actionTypes.getWeaponItem, this.weapon)
        .then(() => {
          this.$emit('close');
        })
        .catch((er) => {
          console.log('', er);
        });
    },
  },
};
</script>

<style></style>
